<!--学生考试首页-->
<template>
  <div id="student">
    <el-row class="padding-50">
      <el-col :span="24">
        <ul class="list">
          <li class="logo"><i class="iconfont icon-kaoshi"></i><span>Online-Exam-System</span></li>
          <a href="javascript:;" @click="exam()"><li>我的试卷</li></a>
          <a href="javascript:;" @click="practice()"><li>我的练习</li></a>
          <router-link to="/scoreTable"><li>我的分数</li></router-link>
          <router-link to="/message"><li>聊天室</li></router-link>
          <router-link to="/errorinfo"><li>错题详情</li></router-link>
          <router-link to="/rank"><li>成绩详情</li></router-link>
          <li class="right" @mouseenter="flag = !flag" @mouseleave="flag = !flag">
            <a href="javascript:;">

              <img :src="imageUrl" style="border-radius: 50%; width: 50px;height: 50px;">
              {{user.userName}}</a>
            <div class="msg" v-if="flag">
              <p @click="manage()">管理中心</p>
              <p class="exit" @click="exit()">退出</p>
            </div>
          </li>
        </ul>
      </el-col>
    </el-row>
    <!--路由区域-->
    <div class="main">
      <router-view></router-view>
    </div>
    <v-footer></v-footer>
  </div>
</template>

<script>
import myFooter from "@/components/student/myFooter"
import {mapState} from 'vuex'
export default {
  components: {
    "v-footer": myFooter
  },
  data() {
    return {
      flag: false,
      user: {},
      imageUrl:''
    }
  },
  created() {
    this.userInfo()
    this.init()
    this.updateAvatar()
  },
  methods: {
    init(){
      let ws=new WebSocket(this.$websocketUrl,this.$jwtDecode(sessionStorage.getItem("token")).id)
              //接收数据
              ws.onmessage=(msg)=>{
                let data=JSON.parse(msg.data)
                if(data.message==='is_lock')
                  {
                    // this.$message({
                    //   message: '有人在登录',
                    //   type: 'success'
                    // })
                    this.open()
                  }
                // console.log(data)
                // if(data.type==='被登录')
                //   console.log(data.message)
              }
    },
    open() {
        this.$confirm('系统检测到有人在登录你的账号, 是否退出?', '提示', {
          confirmButtonText: '退出',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          // this.$message({
          //   type: 'success',

          // });
          this.exit()
        }).catch(() => {
          this.$message({
            type: 'success',
            message: '已取消'
          });
        });
      },
      //头像动态更新
      updateAvatar(){
        setInterval(()=>{
          this.imageUrl=sessionStorage.getItem("image")
        },1000)

      },


    exit() {  //退出登录


      this.$axios({
        url: '/${process.env.peopleApi}/logout/'+this.user.studentId,
        method: 'put'
      }).then(res => {
        console.log(res)
        if(res.data.code ==200) {
          this.$router.push({path:"/"}) //跳转到登录页面
          let ws=new WebSocket(this.$websocketUrl,this.$jwtDecode(sessionStorage.getItem("token")).id)
          ws.CLOSED
          sessionStorage.removeItem('token');
          sessionStorage.removeItem("image")
          sessionStorage.removeItem("role")
       sessionStorage.removeItem("activeInfo")
       sessionStorage.removeItem("index")
       sessionStorage.removeItem("now")
          this.$message({
            message: '退出成功',
            type: 'success'
          })
        }
      })

    },
    manage() {  //跳转到修改密码页面
      this.$router.push({path: '/manager'})
    },
    userInfo() {
      let studentName = this.$jwtDecode(sessionStorage.getItem("token")).name
      let studentId = this.$jwtDecode(sessionStorage.getItem("token")).id
      console.log(`studentId${studentId}`)
      console.log(`studentName ${studentName}`)
      this.user.userName = studentName
      this.user.studentId = studentId
      this.imageUrl=this.$jwtDecode(sessionStorage.getItem("token")).image

    },
    practice() { //跳转练习模式
      let isPractice = true
      this.$store.commit("practice", isPractice)
      this.$router.push({path:'/startExam'})
    },
    exam() { //跳转考试模式
     let isPractice = false
      this.$store.commit("practice", isPractice)
      this.$router.push({path:'/student'})
    }
  },
  computed:mapState(["isPractice"])
}

</script>

<style scoped>
.right .icon {
  margin-right: 6px;
}
#student .padding-50 {
  margin: 0 auto;
  padding: 0 50px;
  box-shadow: 0 0 10px 4px rgba(1,149,255,0.1);
  background-color: #fff;
}
.list a {
  text-decoration: none;
  color: #334046;
}
li {
  list-style: none;
  height: 60px;
  line-height: 60px;
}
#student .list{
  display: flex;
}
#student .list li {
  padding: 0 20px;
  cursor: pointer;
}
#student .list li:hover {
  background-color: #0195ff;
  transition: all 2s ease;
}
#student .list li:hover a {
  color: #fff;
}
#student .list .right {
  margin-left: auto;
  position: relative;
}
#student .list li.right :hover a {
  color: #000;
}
#student .list .logo {
  display: flex;
  font-weight: bold;
  color: #2f6c9f;
}
#student .list .logo i {
  font-size: 50px;
}
.right .msg {
  text-align: center;
  position: absolute;
  top: 60px;
  left: 0px;
  display: flex;
  flex-direction: column;
  border-radius: 2px;
  border-bottom: 3px solid #0195ff;
  background-color: #fff;
}
.right .msg p {
  height: 40px;
  line-height: 40px;
  width: 143px;
}
.right .msg p:hover {
  background-color: #0195ff;
}
</style>



